body {
  margin: 0;
  padding: 0;
  font: 16px "PFBagueSansPro", sans-serif;
  color: #252525;
  background-color: #fafafa;
  line-height: 1.5;
  min-width: 320px;
  width: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  a {
    text-decoration: none!important; }

  main {
    padding: 89px 50px 0;
    position: relative;
    z-index: 1;
    @media (max-width: 1199px) {
      padding: 89px 0 0; }
    @media (max-width: 991px) {
      padding: 81px 0 0; }
    @media (max-width: 767px) {
      padding: 56px 0 0; }
    h1 {
      font-size: 60px;
      line-height: 71px;
      font-weight: bold;
      @media (max-width: 1199px) {
        font-size: 50px;
        line-height: 1.18; }
      @media (max-width: 767px) {
        font-size: 34px; } }

    h2 {
      font-size: 48px;
      letter-spacing: 1px;
      line-height: 1.18;
      font-weight: bold;
      @media (max-width: 1199px) {
        font-size: 40px; }
      @media (max-width: 767px) {
        letter-spacing: 0.25px;
        font-size: 34px; } }

    h3 {
      font-family: "PFBagueSansPro";
      font-size: 24px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.17; } } }

.italic_link {
  color: #252525;
  font-family: Spectral;
  font-size: 18px;
  font-weight: 500;
  font-stretch: normal;
  font-style: italic;
  line-height: 28px;
  transition: all $animation-speed ease-in-out 0s;
  box-shadow: inset 0 0 0 rgba(25, 105, 223, 0%), 0 1px 0 rgba(25, 105, 223, 40%);
  margin: 7px 0;
  display: block;
  &:hover {
    color: #252525;
    box-shadow: inset 0 0 0 rgba(25, 105, 223, 0%), 0 2px 0 #1969df; } }
.brief {
  display: inline-flex;
  align-self: flex-start;
  flex-flow: column;
  transition: all $animation-speed ease-in-out 0s;
  backface-visibility: hidden;
  &:hover {
    opacity: 0.75; }
  &__desc {
    font-style: italic;
    font-family: "Spectral", sans-serif; }
  &__text {
    text-transform: uppercase;
    font-weight: bold;
    font-size: 20px;
    letter-spacing: 0.3px; } }

.MuiButton-root {
  font-family: "PFBagueSansPro" !important; }

.btn {
  position: relative;
  height: 42px!important;
  line-height: 42px!important;
  padding: 0 16px!important;
  border-radius: 21px!important;
  display: inline-flex;
  align-items: center;
  color: #fff;
  background-color: #1969df!important;
  transition: all $animation-speed ease-in-out 0s;
  backface-visibility: hidden;
  cursor: pointer;
  border: 0;
  box-shadow: none!important;
  &.Mui-disabled {
    background-color: #dedede!important;
    opacity: 1!important; }
  .MuiButton-label {
    svg {
      width: 18px;
      height: 18px;
      fill: #ffffff;
      margin-right: 12px; }
    span {
      color: #ffffff;
      text-transform: uppercase;
      font-size: 14px;
      font-weight: bold;
      letter-spacing: 0.75px;
      line-height: 1;
      font-family: "PFBagueSansPro"!important;
      margin-bottom: -2px; } }
  &:hover {
    background-color: #2f76e1; } }

.MuiInputBase-input {
  font-family: PFBagueSansPro !important; }

.container-fluid {
  padding: 0 110px;
  @media (max-width: 1199px) {
    padding: 0 50px; }
  @media (max-width: 991px) {
    padding: 0 30px; }
  @media (max-width: 575px) {
    padding: 0 15px; } }

.w-bottom-bar-right.w-css-reset {
  display: none !important; }

.col-2_block {
  display: flex;
  flex-wrap: wrap;
  //align-items: center
  align-items: stretch;
  & > div {
    width: 50%;
    @media (max-width: 767px) {
      width: 100%; }
    &.media,
    &.color_block {
      position: relative;
      pointer-events: none;
      * {
        pointer-events: none; }
      &:first-child {
        .wistia_responsive_padding,
        .color_block__content {
          position: relative;
          margin-left: -20px;
          margin-right: -55px;
          width: calc(100% + 20px + 55px);
          @media (max-width: 1199px) {
            margin-left: -15px;
            width: calc(100% + 15px + 55px); }
          @media (max-width: 991px) {
            margin-left: -15px;
            margin-right: -30px;
            width: calc(100% + 15px + 30px); }
          @media (max-width: 575px) {
            margin-left: -15px;
            margin-right: -15px;
            width: calc(100% + 15px + 15px); } } }
      &:last-child {
        .wistia_responsive_padding,
        .color_block__content {
          position: relative;
          margin-right: -20px;
          margin-left: -55px;
          width: calc(100% + 20px + 55px);
          @media (max-width: 1199px) {
            margin-right: -15px;
            width: calc(100% + 15px + 55px); }
          @media (max-width: 991px) {
            margin-right: -15px;
            margin-left: -30px;
            width: calc(100% + 15px + 30px); }
          @media (max-width: 575px) {
            margin-right: -15px;
            margin-left: -15px;
            width: calc(100% + 15px + 15px); } } } }
    &.content {
      position: relative;
      .title {
        position: relative;
        margin-bottom: 26px;
        padding-bottom: 16px;
        @media (max-width: 575px) {
          margin-bottom: 32px; }
        &:after {
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          width: 66px;
          height: 12px;
          background-image: url('./pages/assets/wave.png');
          background-size: contain; } }
      .text {
        p {
          margin: 0;
          font-size: 16px;
          line-height: 1.5;
          & + p {
            margin-top: 12px; } }
        & + * {
          margin-top: 32px;
          @media (max-width: 575px) {
            margin-top: 24px;
            @media (max-width: 575px) {
              margin-bottom: 0; } } } }
      .italic_link {
        display: inline-block; }
      &:first-child {
        padding-right: calc(50px + 55px);
        @media (min-width: 1280px) {
          padding-right: calc(120px + 55px); }
        @media (max-width: 991px) {
          padding-right: calc(30px + 30px); }
        @media (max-width: 767px) {
          padding-right: 0;
          margin-bottom: 40px; }
        @media (max-width: 575px) {
 } }          //padding: 0
      &:last-child {
        padding-left: calc(50px + 55px);
        @media (min-width: 1280px) {
          padding-left: calc(120px + 55px); }
        @media (max-width: 991px) {
          padding-left: calc(30px + 30px); }
        @media (max-width: 767px) {
          margin-top: 45px;
          padding-left: 0; }
        @media (max-width: 575px) {
          padding: 0; } } }

    &.media,
    &.color_block,
    &.content {
      display: flex;
      flex-wrap: wrap;
      align-items: center; } } }

.col-2_block {
    & .color_block {
      pointer-events: auto;
      .color_block__content {
        position: relative;
        height: 100%;
        padding: 48px 40px;
        color: #fff;
        @media (max-width: 767px) {
          padding: 40px; }
        @media (max-width: 575px) {
          padding: 40px 15px; }
        &:after {
          content: "";
          display: block;
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background: #1969df;
          z-index: 0; }
        .text {
          display: flex;
          flex-wrap: wrap;
          position: relative;
          z-index: 2;
          & > div {
            width: 50%;
            @media (max-width: 991px) {
              width: 100%;
              & + div {
                margin-top: 40px; } }
            h3 {
              position: relative;
              padding-top: 24px;
              margin-bottom: 24px;
              &:before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                background-image: url(./pages/assets/wave-white.png);
                background-size: contain;
                width: 66px;
                height: 12px; } }
            span {
              display: block;
              line-height: 28px;
              position: relative;
              padding-left: 16px;
              & + span {
                margin-top: 2px; }
              &:before {
                content: "—";
                font-family: "PFBagueSansPro";
                font-size: 16px;
                line-height: 28px;
                display: block;
                position: absolute;
                top: 0;
                left: 0; } } } } } } }

.bodyWrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  main {
    flex: 1 0 auto; }
  footer {
    flex: 0 0 auto; } }

.title_video_header {
  width: 100%;
  position: relative;
  min-height: calc(100vh - 86px);
  display: flex;
  align-items: center;
  background: #fafafa;
  @media (max-width: 1199px) {
    min-height: calc(100vh - 81px); }
  @media (max-width: 767px) {
    min-height: calc(100vh - 56px); }
  @media (max-width: 575px) {
    background: no-repeat center;
    background-size: cover; }
  video {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    filter: grayscale(1);
    height: 100%;
    object-fit: cover;
    //opacity: 0.985
    z-index: 0;
    @media (max-width: 575px) {
      opacity: 0; } }
  &__inner {
    width: 100%;
    padding: 0 30px;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    color: #fff;
    h1 {
      @media (max-width: 767px) {
        font-size: 48px; } } }
  .arrow_down {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 3;
    padding: 24px;
    cursor: pointer;
    svg {
      display: block;
      width: 26px;
      height: 26px;
      transition: all $animation-speed; }
    &:hover {
      svg {
        fill: #1969df; } } } }
